body {
  background-color: #f2f2f2;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  color: #333;
}

#titleH {
  text-align: center;
}

#titleH h1 {
  margin-bottom: 5px;
  margin-right: 60px;
}

#titleH p {
  margin-top: -10px;
  margin-inline-start: -60px;
}

nav {
  background-color: #333;
  overflow: hidden;
}

nav a {
  float: left;
  color: #292929;
  margin: 0 10px;
  text-decoration: none;
}

nav a:hover {
  color: #000;
}

nav .selected {
  background-color: #d7d7d7;
}

.top-nav {
  background-color: #f2f2f2;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.profile-info {
  align-items: center;
  display: flex;
}

.profile-info img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 10px;
}

#mainContent {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.post {
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
}

.post img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

#userID {
  margin: 0;
}

#postText {
  margin-top: 10px;
}

.commentForm {
  display: flex;
}

[id^="commentInput_"] {
  color: #333;
  background-color: #e6e6e6;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: calc(100% - 30px);
  padding: 8px 15px;
  transition: background-color .3s;
}

[id^="commentInput_"]:focus {
  background-color: #fff;
  border-color: #4caf50;
  outline: none;
}

.commentBtn {
  color: #fff;
  cursor: pointer;
  background-color: #73a873;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  transition: background-color .3s;
}

.commentBtn:hover {
  background-color: #598f59;
}

#postForm {
  margin-bottom: 20px;
}

#postForm input, #postForm textarea, #postForm button {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  display: block;
}

#postForm button {
  color: #fff;
  cursor: pointer;
  background-color: #73a873;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  transition: background-color .3s;
}

#postForm button:hover {
  background-color: #598f59;
}

#commentForm textarea, #commentForm button {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  display: block;
}

#commentForm button {
  color: #fff;
  cursor: pointer;
  background-color: #73a873;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  transition: background-color .3s;
}

#commentForm button:hover {
  background-color: #598f59;
}

.commentList {
  margin-top: 10px;
}

.comment {
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 5px;
  padding: 10px;
}

.comment:nth-child(2n) {
  background-color: #e6e6e6;
}

#userDropdownContainer {
  text-align: left;
  margin: 3px 3px 3px 19px;
}

label {
  color: #333;
  margin-right: 2px;
  font-size: 16px;
  font-weight: bold;
}

#userDropdown {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  padding: 5px;
  font-size: 10px;
}

#userDropdown option {
  padding: 8px;
  font-size: 16px;
}
