/* Generella förbättringar */
body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  background-color: #f2f2f2;
  /* Ljus bakgrundsfärg */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333;
}

#titleH {
  text-align: center;
}

#titleH h1 {
  margin-bottom: 5px;
  margin-right: 60px;

}

#titleH p {
  margin-top: -10;
  margin-inline-start: -60px;
}

nav {
  background-color: #333;
  overflow: hidden;
}

nav a {
  float: left;
  color: rgb(41, 41, 41);
  /* justify-content: center; */
  /* padding: 14px 20px; */
  margin: 0 10px;
  /* lägg till marginaler */
  text-decoration: none;
}

nav a:hover {
  color: black;
}

nav .selected {
  background-color: rgb(215, 215, 215);
  /* Ange färgen för den valda länken */
}


.top-nav {
  background-color: #f2f2f2;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.profile-info {
  display: flex;
  align-items: center;
}

.profile-info img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 10px;
}


/* Inlägg och kommentarer */
#mainContent {
  margin: 0 auto;
  max-width: 800px;
  padding: 20px;
}

.post {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
}

.post img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

#userID {
  margin: 0;
}

#postText {
  margin-top: 10px;
}

.commentForm {
  display: flex;
}

/*Måste använa [id^= eftersom det är måsvinge i id taggen.*/
[id^="commentInput_"] {
  background-color: #e6e6e6;
  color: #333;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 8px 15px;
  transition: background-color 0.3s;
  width: calc(100% - 30px);
}

[id^="commentInput_"]:focus {
  outline: none;
  background-color: #fff;
  border-color: #4CAF50;
}

.commentBtn {
  background-color: rgb(115, 168, 115);
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.commentBtn:hover {
  background-color: rgb(89, 143, 89);
}

#postForm {
  margin-bottom: 20px;
}

#postForm input,
#postForm textarea,
#postForm button {
  display: block;
  margin-bottom: 10px;
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

#postForm button {
  background-color: rgb(115, 168, 115);
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

#postForm button:hover {
  background-color: rgb(89, 143, 89);
}

#commentForm textarea,
#commentForm button {
  display: block;
  margin-bottom: 10px;
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

#commentForm button {
  background-color: rgb(115, 168, 115);
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

#commentForm button:hover {
  background-color: rgb(89, 143, 89);
}

/* Kommentarernas container */
.commentList {
  margin-top: 10px;
}

/* Enskild kommentar */
.comment {
  background-color: #f9f9f9;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;

}

/* Jämn kommentar */
.comment:nth-child(even) {
  background-color: #e6e6e6;
}

#userDropdownContainer {
  margin: 3px;
  text-align: left;
  margin-left: 19px;
}

label {
  color: #333;
  font-size: 16px;
  font-weight: bold;
  margin-right: 2px;
}

#userDropdown {
  padding: 5px;
  font-size: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #fff;
  outline: none;
  cursor: pointer;
}

#userDropdown option {
  padding: 8px;
  font-size: 16px;
}